var convert = require('xml-js');

const weekDays = [
  '',
  '',
  '',
  '',
  '',
  '',
];

const teachers = [
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
  ' ..',
];
const lessons = {
  high: [],
  low: [],
};
let file;

const fileInput = document.querySelector('.file-input');
const searchSelect = document.querySelector('.select');
teachers.forEach((teacher) => {
  searchSelect.insertAdjacentHTML(
    'beforeEnd',
    `<option value="${teacher}">${teacher}</option>`
  );
});
const button = document.querySelector('.button');
const block = {
  low: document.getElementById('low'),
  high: document.getElementById('high'),
};
const reader = new FileReader();

button.addEventListener('click', () => {
  const inputValue = searchSelect.value;
  deleteBlocks();
  deleteArrItems();
  if (inputValue.trim() && file) {
    reader.readAsText(file);
    reader.onload = function () {
      const fileJsonString = convert.xml2json(reader.result, {
        compact: true,
        spaces: 4,
      });
      const fileJson = JSON.parse(fileJsonString);
      const rowsArr = fileJson.Workbook.Worksheet[0].Table.Row;
      let weekDay;
      let group;
      rowsArr.forEach((row) => {
        if (Array.isArray(row.Cell)) {
          row.Cell.forEach((cell, id) => {
            if (
              cell.hasOwnProperty('Data') &&
              weekDays.includes(cell.Data._text)
            ) {
              weekDay = cell.Data._text;
            }
            if (
              cell.hasOwnProperty('Data') &&
              cell.Data._text.includes('')
            ) {
              group = cell.Data._text;
            }
            if (cell.hasOwnProperty('Data') && cell.Data._text === inputValue) {
              const date = new Date(row.Cell[id - 7].Data._text);
              const time = `${date.getHours()}:${date.getMinutes()}`;
              const lessonObj = {
                dayClass: getDayClass(weekDay),
                weekDay,
                time,
                week: getLessonValue(row.Cell[id - 6]),
                lessonName: getLessonValue(row.Cell[id - 5]),
                building: getLessonValue(row.Cell[id - 4]),
                cabinet: getLessonValue(row.Cell[id - 3]),
                lessonType: getLessonValue(row.Cell[id - 2]),
                teacher: getLessonValue(cell),
                group,
              };
              if (lessonObj.week === '') {
                lessons.low.push(lessonObj);
              } else {
                lessons.high.push(lessonObj);
              }
            }
          });
        }
      });
      const lowLessons = formatLessons(lessons.low);
      const highLessons = formatLessons(lessons.high);
      console.log(lowLessons);
      lowLessons.forEach((lessonObj) => {
        block.low.insertAdjacentHTML(
          'beforeEnd',
          `
            <div class="block ${lessonObj.dayClass}">
               <p class="text">${lessonObj.weekDay}</p>
               <p class="text">: ${lessonObj.time}</p>
               <p class="text">: ${lessonObj.week}</p>
               <p class="text">: ${lessonObj.building}</p>
               <p class="text">: ${lessonObj.cabinet}</p>
               <p class="text">: ${lessonObj.lessonName}</p>
               <p class="text">: ${lessonObj.lessonType}</p>
               <p class="text">: ${lessonObj.teacher}</p>
             </div>`
        );
      });
      highLessons.forEach((lessonObj) => {
        block.high.insertAdjacentHTML(
          'beforeEnd',
          `
            <div class="block ${lessonObj.dayClass}">
               <p class="text">${lessonObj.weekDay}</p>
               <p class="text">: ${lessonObj.time}</p>
               <p class="text">: ${lessonObj.week}</p>
               <p class="text">: ${lessonObj.building}</p>
               <p class="text">: ${lessonObj.cabinet}</p>
               <p class="text">: ${lessonObj.lessonName}</p>
               <p class="text">: ${lessonObj.lessonType}</p>
               <p class="text">: ${lessonObj.teacher}</p>
             </div>`
        );
      });
    };
    reader.error = function () {
      alert('');
    };
  } else {
    alert('-  !');
  }
});

fileInput.addEventListener('change', (e) => {
  file = e.target.files[0];
});

function deleteBlocks() {
  if (block.low.hasChildNodes() || block.high.hasChildNodes()) {
    const blocks = document.querySelectorAll('.block');
    blocks.forEach((element) => {
      element.remove();
    });
  }
}

function deleteArrItems() {
  lessons.low.splice(0);
  lessons.high.splice(0);
}

function getLessonValue(cell) {
  if (cell.hasOwnProperty('Data')) {
    return cell.Data._text;
  } else {
    return '';
  }
}

function isDuplicateLesson(firstLeson, secondLesson) {
  return (
    firstLeson.building === secondLesson.building &&
    firstLeson.cabinet === secondLesson.cabinet &&
    firstLeson.lessonName === secondLesson.lessonName &&
    firstLeson.lessonType === secondLesson.lessonType &&
    firstLeson.time === secondLesson.time
  );
}

function formatLessons(lessons) {
  return lessons.filter((item, i, arr) => {
    console.log(item.building);
    if (i < arr.length - 1) {
      if (isDuplicateLesson(item, arr[i + 1])) {
        return false;
      }
    }
    return true;
  });
}

function getDayClass(weekDay) {
  switch (weekDay) {
    case '':
      return 'block--monday';
    case '':
      return 'block--tuesday';
    case '':
      return 'block--wednesday';
    case '':
      return 'block--thursday';
    case '':
      return 'block--friday';
    case '':
      return 'block--saturday';
    default:
      return '';
  }
}
